body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .blob-blue {
    background: #456BD9;
    border: 0.1875em solid #0F1C3F;
    border-radius: 50%;
    height: 1.75em;
    width: 1.75em;
    margin: auto;
  }

  .blob-red {
    background: #d94545;
    border: 0.1875em solid #0F1C3F;
    border-radius: 50%;
    height: 1.75em;
    width: 1.75em;
    margin: auto;
  }

  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 64px;
    height: 64px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 64px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column;
  }
  
  .game-info {
    margin: 10 auto;
  }
  
  .game-board{
    margin: 10 auto;
  }